.footer{
    background-color: var(--button-background-color);
    border-top: 1px solid rgba(0,0,0,0.1);
}
.footer_container{
    padding:2rem 0 6rem;
}
.footer_title,
.footer_link{
    color:var(--title-color);
}
.footer_title{
    text-align: center;
    margin-bottom: var(--mb-2);
}
.footer_link:hover{
    color:var(--title-color-dark);
}
.footer_list{
    display:flex;
    justify-content:center;
    column-gap: 1rem;
    margin-bottom: var(--mb-2);
    text-align: center;
}
.footer_social{
    display:flex;
    justify-content:center;
    column-gap: 1.25rem;
}
.footer_social-icon i{
    color: var(--title-color);
}
.footer_social-link{
    background-color: var(--title-color);
    color:var(--container-color);
    font-size:1.125rem;
    padding:0.4rem;
    border-radius: 0.5rem;
    display:inline-flex;
}
.footer_social-link:hover{
    background-color: var(--title-color-dark);
}
.footer_copy{
    display:block;
    margin-top: 1.5rem;
    color:var(--title-color);
    text-align: center;
    font-size:var(--smaller-font-size);
}
@media screen and (max-width: 992px) {
    .footer_social-link{
        padding:0.25rem;
        border-radius: 0.25rem;
        font-size:1rem;
    }
} 
@media screen and (max-width: 576px) {
    .contact_info{
        grid-template-columns: 1fr;
    }
    .contact_form{
        width: 100%;
    }
    .contact_card{
        width: 350px;
        margin-left: 1rem;
    }
    .contact_form-input{
        width: 350px;
        margin-left: 1rem;
    }
    .footer_list{
        display: grid;
        grid-template-columns: repeat(3,2fr);
        gap: 1rem;
        justify-content: center;
        align-items: center;
    }
  }
  
  @media screen and (max-width: 350px) {
    :root {
      --big-font-size: 2.25rem;
    }
  
    .container {
      margin-left: var(--mb-1);
      margin-right: var(--mb-1);
    }
    
  }